<template>
	<div class="system">
		<div class="system-left">
			<p class="title-big"></p>
			<p class="title-small">
				
			</p>
			<p class="title-big"></p>
			<p class="title-small  title-small_2">
				   
			</p>
			<div class="title-small">
				 
			</div>
			<p class="title-big"></p>
			<p class="title-small">   
			</p>

		</div>
		<div class="system-right">
			<div class="img1"><img :src="system_1" /></div>
		</div>
	</div>
</template>

<script>
	import system_1 from '../img/shtx.png';
	export default {
		data: function() {
			return {
				system_1: system_1,
			};
		},
        mounted: function() {
			this.$emit("setCrumb", "");
		}
	};
</script>

<style lang="less" scoped>
	.system {
		.system-left {
			width: 500px;
			float: left;
			.title-big {
				font-weight: 500;
				font-size: 16px;
				color: #92c5d7;
				padding-bottom: 10px;
				span {
					font-size: 12px;
					color: #96938D;
				}
			}
			.title-small {
				font-size: 16px;
				padding-bottom: 20px;
			}
			.title-small_2 {
				padding-bottom: 0px;
			}
		}
		.system-right {
			width: 300px;
			float: left;
			.img1 {
				margin-top: 30px;
				padding-bottom: 15px;
			}
		}
	}
</style>


