<template>
	<div class="main">
		<div class="main-list">
			<!--<div class="top">
				<span class="text">
					<small>About us</small>
				</span>
				<span class="fa fa-angle-right"></span>
			</div>-->
			<router-link to="/about/introduce" class="link">
				<div class="top">
					<span class="text">
					<small>About us</small>
				</span>
					<span class="fa fa-angle-right"></span>
				</div>
			</router-link>
			<router-link to="/about/introduce" class="about">
				<img class="logo" :src="logo_jpg" alt="Logo">
				<div class="content">
					<p class="title">{{about.title}}</p>
					<div class="describe">
						<p class="text-indent">{{about.summary}}</p>
						<p class="detail text-indent">{{about.detail}}</p>
					</div>
				</div>
			</router-link>
		</div>
		<div class="main-list">
			<!--<div class="top">
				<span class="text">
					<small>Company news</small>
				</span>
				<span class="fa fa-angle-right"></span>
			</div>-->
			<router-link to="/news/index" class="link">
				<div class="top">
					<span class="text">
					<small>Company news</small>
				</span>
					<span class="fa fa-angle-right"></span>
				</div>
			</router-link>
			<div class="news">
				<ul>
					<li class="news-item" v-for="(item,index) in news" :key="index" @click="showNews(index)">
						<div class="title">{{item.title}}</div>
						<p class="text-indent detail">{{item.content}}</p>
					</li>
				</ul>
			</div>
		</div>
		<div class="main-list">
			<!--<div class="top">
				<span class="text">
					<small>Credit information service</small>
				</span>
				<span class="fa fa-angle-right"></span>
			</div>-->
			<router-link to="/financial/credit" class="link">
				<div class="top">
					<span class="text">
					<small>Credit information service</small>
				</span>
					<span class="fa fa-angle-right"></span>
				</div>
			</router-link>
			<div class="service">
				<ul>
					<li class="service-item" v-for="(item,index) in service" :key="index">
						<div class="avatar">
							<img :src="item.imgUrl">
						</div>
						<div class="content">
							<router-link :to="item.url">
								<div class="title">{{item.title}}</div>
								<p class="text-indent detail">{{item.content}}</p>
							</router-link>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import avatar_index_01 from "./img/avatar_index_01.jpg";
	import avatar_index_02 from "./img/avatar_index_02.jpg";
	import logo_jpg from "./img/logo.jpg";
	export default {
		data: function() {
			return {
				logo_jpg: logo_jpg,
				about: {
					title: "",
					summary: `
				
				`,
					detail: `20167205000
				...`
				},
				news: [{
						title: "",
						id: 1,
						content: `201795
		  ...`
					},
					{
						title: "SEMMA",
						id: 2,
						content: `201784
		  ...`
					},
					{
						title: "",
						id: 3,
						content: `2017763...`
					}
				],
				service: [{
						url: "/financial/credit",
						title: "",
						imgUrl: avatar_index_01,
						content: ` 
		  ...`
					},
					{
						url: "/government/benevolent",
						title: "",
						imgUrl: avatar_index_02,
						content: `
		  ...`
					}
				]
			};
		},
		methods: {
			showNews: function(index) {
				this.$router.push("/news/index/" + "detail_" + index);
			}
		}
	};
</script>

<style lang="less" scoped>
	.main {
		display: flex;
		width: 1000px;
		margin: 0 auto;
		padding: 35px 0 44px;
		justify-content: space-between;
		.main-list {
			width: 320px;
			.top {
				position: relative;
				padding: 8px 15px;
				border-radius: 5px;
				color: rgb(255, 255, 255);
				background: rgb(179, 27, 36);
				.text {
					vertical-align: middle;
					font-size: 16px;
					small {
						margin-left: 10px;
					}
				}
				.fa-angle-right {
					position: absolute;
					right: 10px;
					font-size: 21px;
				}
			}
			.about,
			.news,
			.service {
				height: 364px;
				overflow: hidden;
			}
			.about {
				overflow: hidden;
				display: block;
				.logo {
					margin: 9px 5px;
				}
				.content {
					padding: 0 5px;
					.title {
						font-size: 16px;
					}
					.describe {
						font-size: 14px;
						line-height: 1.75;
						&:hover {
							color: #b31b24;
						}
						.detail {
							overflow: hidden;
							text-overflow: ellipsis;
							display: -webkit-box;
							-webkit-line-clamp: 4;
							-webkit-box-orient: vertical;
						}
					}
				}
			}
			.news {
				.news-item {
					position: relative;
					max-height: 92px;
					overflow: hidden;
					margin: 25px 0 30px;
					padding: 0 5px;
					cursor: pointer;
					&:hover {
						color: #b31b24;
					}
					&:last-child {
						margin-bottom: 0;
					}
					&::after {
						content: "";
						position: absolute;
						bottom: -15px;
						border-bottom: 2px dashed #e2e2e2;
						width: 100%;
					}
					.title {
						font-weight: bold;
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
					}
					.detail {
						overflow: hidden;
						line-height: 1.75;
						font-size: 14px;
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: 3;
						-webkit-box-orient: vertical;
					}
				}
			}
			.service {
				.service-item {
					display: flex;
					margin: 15px 0;
					padding: 0 5px;
					&:last-child {
						margin-bottom: 0;
					}
					.avatar {
						display: flex;
						flex: 1;
						align-items: center;
					}
					.content {
						flex: 1;
						margin-left: 10px;
						.title {
							font-weight: bold;
						}
						.detail {
							overflow: hidden;
							max-height: 141px;
							line-height: 1.75;
							font-size: 14px;
							text-overflow: ellipsis;
							display: -webkit-box;
							-webkit-line-clamp: 6;
							-webkit-box-orient: vertical;
							&:hover {
								color: #b31b24;
							}
						}
					}
				}
			}
		}
	}
</style>


