		},
        mounted: function() {
			this.$emit("setCrumb", "");
		}
	};
</script>
<style lang="less" scoped>
	.credit {
		.credit-left {
			width: 590px;
			float: left;
			line-height: 26px;
			font-size: 16px;
			text-indent: 2em;
			padding-bottom: 15px;
		}
		.credit-right {
