<template>
	<div class="introduce">
		<p></p>
		<p>20167205000</p>
		<div  class="img1"><img  :src="introduce_1" /></div>
		<p>BIIA2016APEC201520162016</p>
		<p>ISN5873IEEE Fellow1</p>
		<div class="img2"><img  :src="introduce_2" /></div>
		<p>2014-2020  D&B </p>
		<p></p>
    </div>
</template>

<script>
	import introduce_1 from '../img/company-1.png';
	import introduce_2 from '../img/company-2.png';
	export default {
		data: function() {
			return {
                introduce_1: introduce_1,
				introduce_2: introduce_2,
			};
		},
		mounted: function() {
			this.$emit("setCrumb", "");
		}
	};
</script>

<style lang="less" scoped>
.introduce{
	margin-bottom: 23px;
  p{
  	font-size:16px;
  	text-indent: 2em;
  	padding-bottom:5px ;
  	line-height: 1.75;
  	color: #3A3B3A;
  }
  .img1{
  	height: 340px;
  	text-align: center;
  }
  .img2{
  	height: 234px;
  	text-align: center;
  }
}  
</style>


