import newsDetail2 from "./newsDetail/newsDetail2";
import newsDetail3 from "./newsDetail/newsDetail3";
import newsDetail4 from "./newsDetail/newsDetail4";
import newsDetail5 from "./newsDetail/newsDetail5";
