          id: "1015",
          date: "2016-08-25",
          title: ""
        }
      ]
    };
  },
  methods: {
    showDetail: function(current) {
      this.$router.push("/news/index/" + "detail_" + current);
    },
    backList() {
      this.$router.push("/news/index/");
    }
  },
  beforeRouteEnter(to, from, next) {
    var vm = this;
    next(vm => {
      if (to.params.id) {
        vm.showList = false;
        vm.current =
          "newsDetail" + (Number(to.params.id.replace("detail_", "")) + 1);
      } else {
        vm.showList = true;
      }
    });
  },
  mounted: function() {
    this.$emit("setCrumb", "");
  }
};
</script>

<style lang="less" scoped>
.company-news {
  .news-wrapper {
    li {
      font-size: 16px;
      color: #7d7a74;
      margin-bottom: 12px;
      border-bottom: 1px dotted transparent;
      div {
        margin-top: 12px;
      }
      &:hover {
        transition: all 0.3s ease-in;
        cursor: pointer;
        border-color: #2d8cf0;
        color: #2d8cf0;
      }
    }
    .title {
      float: left;
    }
    .time {
      float: right;
    }
    .footer {
      position: relative;
      bottom: 20px;
      color: #777;
    }
  }
}
</style>


