<template>
	<div class="culture">
		<div class="culture-left">
			<p class="title-big"> <span>Company Philosophy</span></p>
			<p class="title-small"></p>
			<p class="title-big"> <span> Our Vison</span></p>
			<p class="title-small"></p>
			<p class="title-big"><span>Our Mission</span></p>
			<p class="title-small"></p>
			<p class="title-big"> <span> Core Values </span></p>
			<p class="title-small"></p>
			<p class="title-big"> <span>Organization Development </span></p>
			<p class="title-small"></p>
		</div>
		<div class="culture-right">
			<div class="img1"><img :src="culture_1" /></div>
			<div class="img2"><img :src="culture_2" /></div>
		</div>
	</div>
</template>

<script>
	import culture_1 from '../img/logo-right.png';
	import culture_2 from '../img/img-right.png';
	export default {
		data: function() {
			return {
				culture_1: culture_1,
				culture_2: culture_2,
			};
		},
		mounted: function() {
			this.$emit("setCrumb", "");
		}
	};
</script>

<style lang="less" scoped>
	.culture {
		.culture-left {
			width: 500px;
			float:left;
			.title-big {
				font-weight: 500;
				font-size: 16px;
				color: #CD0000;
				padding-bottom: 10px;
				span {
					font-size: 12px;
					color: #96938D;
				}
			}
			.title-small {
				font-size: 14px;
				padding-bottom: 20px;
			}
		}
		.culture-right {
			width: 300px;
			float: left;
		.img1 {
			padding-bottom: 15px;
		}
		}
	}
</style>


